import { Container } from "@mui/material";
import axios from "axios";
import React, { useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import MovieDatils from "../Components/MovieDatils";

const BgContainer = styled.div`
  height: 100vh;
  position: fixed;
  width: 100%;
  z-index: -1;
  top: 0;
`;
const Movie = () => {
  const { id } = useParams();
  const type = id.split("=")[0];
  const code = id.split("=")[1];
  const [movie, setMovie] = useState();
  const [trailer, setTrailer] = useState();
  const [bg, setBg] = useState([
    { rgba: ["255", "255", "255", "255"] },
    { rgba: ["216", "216", "216", "216"] },
  ]);
  const url = "https://api.themoviedb.org/3/";
  const getMovie = async () => {
    await axios
      .get(`${url}${type}/${code}`, {
        params: {
          api_key: process.env.REACT_APP_API_KEY,
          language: "en-US",
          append_to_response: "videos,credits",
        },
      })
      .then((response) => {
        console.log("data", response.data);
        Gettrailer(response.data.videos.results);
        setMovie(response.data);
      })
      .catch((err) => console.log("Error===", err));
  };
  const Bgcolour = (data) => {
    const { rgba } = data;
    return rgba.join(",");
  };
  const Gettrailer = (data) => {
    if (data.length > 0)
      setTrailer(data.find((item) => item.type === "Trailer"));
  };
  useEffect(() => {
    getMovie();
  }, []);

  return (
    <>
      {movie && (
        <>
          <Helmet>
            <meta charSet="utf-8" />
            <title>{movie?.title || movie?.name}</title>
          </Helmet>
          <BgContainer
            style={{ background: `linear-gradient(45deg, rgb(${Bgcolour(bg[0])}), rgb(${Bgcolour(bg[1])}))` }}
          ></BgContainer>
          <Container sx={{ marginBottom: "20px" }}>
            {/* <Player id="xaYJgKiIH0Q"/> */}
            <MovieDatils
              poster_path={movie?.poster_path}
              title={movie?.title || movie?.name}
              release_date={movie?.release_date || movie?.first_air_date}
              vote_average={movie?.vote_average}
              runtime={movie?.runtime}
              genres={movie?.genres}
              spoken_languages={movie?.spoken_languages}
              overview={movie?.overview}
              credits={movie?.credits}
              status={movie?.status}
              production_companies={movie?.production_companies}
              homepage={movie?.homepage}
              trailer={trailer}
              setBg={setBg}
            />
          </Container>
        </>
      )}
    </>
  );
};

export default Movie;
